*,
*::before,
*::after {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0; }

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Iskra", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility; }

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff; }

a {
  color: #000000;
  text-decoration: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
  overflow: hidden; }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    flex-grow: 1; }

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */ }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.scroll-lock-ios {
  position: fixed;
  overflow: hidden; }

.scroll-lock {
  overflow: hidden; }

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none; }
  .no-scrollbar::-webkit-scrollbar {
    display: none; }

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important; }

.container {
  width: 100%;
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 15px; }

.title {
  color: #1F252F;
  margin: 0;
  text-transform: uppercase; }
  .title--h1 {
    font-weight: 700;
    font-size: 45px;
    line-height: 52px; }
    @media (max-width: 767px) {
      .title--h1 {
        font-size: 26px;
        line-height: 30px; } }
  .title--h2 {
    font-weight: 700;
    font-size: 35px;
    line-height: 41px; }
    @media (max-width: 767px) {
      .title--h2 {
        font-size: 22px;
        line-height: 26px; } }

.content-text {
  max-width: 690px;
  font-size: 18px;
  line-height: 24px;
  color: rgba(0, 0, 0, 0.7);
  margin: 0 0 15px 0; }

.zones {
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
  text-transform: uppercase; }
  .zones--blue {
    color: rgba(30, 152, 255, 0.9); }
  .zones--green {
    color: rgba(27, 173, 3, 0.9); }
  .zones--purple {
    color: rgba(181, 30, 255, 0.9); }

.sap-new__heading-title {
  margin: 55px 0 25px 0; }
  @media (max-width: 767px) {
    .sap-new__heading-title {
      margin: 35px 0 20px 0; } }

.sap-new__heading-disclaimer {
  font-size: 24px;
  line-height: 24px;
  color: #000000;
  margin: 0 0 15px 0; }
  @media (max-width: 767px) {
    .sap-new__heading-disclaimer {
      font-size: 20px;
      line-height: 100%; } }

.sap-new__delivery-zones-title {
  margin: 35px 0 15px 0; }
  @media (max-width: 767px) {
    .sap-new__delivery-zones-title {
      margin: 25px 0 15px 0; } }

.sap-new__delivery-zones-link {
  color: rgba(30, 152, 255, 0.9); }
  .sap-new__delivery-zones-link:hover {
    text-decoration: underline; }

.sap-new__delivery-zones-map {
  width: 100%;
  max-width: 690px;
  height: 690px;
  margin: 0 0 55px 0; }
  @media (max-width: 767px) {
    .sap-new__delivery-zones-map {
      height: 400px; } }
  .sap-new__delivery-zones-map iframe {
    width: 100%;
    height: 100%; }

.sap-new__express-delivery-title {
  margin: 35px 0 15px 0; }

.sap-new__time-additional-title {
  margin: 35px 0 15px 0; }

.sap-new__time-additional-list {
  margin: 0;
  padding: 0 0 0 23px; }

